*, :before, :after {
  box-sizing: border-box;
  overflow-wrap: break-word;
  margin: 0;
  line-height: calc(1em + 8px);
}

body {
  font-family: sans-serif;
}

img, picture, video, canvas, svg {
  max-width: 100%;
  height: auto;
  display: block;
}

input, button, textarea, select {
  font: inherit;
  color: inherit;
  letter-spacing: inherit;
  display: block;
}

